<template>
    <div id="app">
        <div class="window_con">
            <div class="title">随堂记录</div>
            <div v-if="isHistory" class="btn_back" @click="backCalendar"><img src="@/assets/icon_back@2x.png" />返回</div>
            <div class="records">
                <vue-scroll>
                    <div class="item" v-for="item in records" :key="item.id">
                        <div class="info">
                            <div class="info_left">
                                <div class="type" v-if="item.type === 0">纸笔互动</div>
                                <div class="type" v-else-if="item.type === 1||item.type === 10">【随堂测】<span
                                        v-if="item.ext">{{ item.ext | decodePaperName }}</span></div>
                                <div class="type" v-else-if="item.type === 2">判断题</div>
                                <div class="type" v-else-if="item.type === 3">单选</div>
                                <div class="type" v-else-if="item.type === 4">多选</div>
                                <div class="type" v-else-if="item.type === 5">抢答</div>
                                <div class="user">{{ item.countStu }}人参与，
                                    <template
                                        v-if="item.status === 'uploading' && item.rate !== 1 && item.countStu">正在上传中...</template>
                                    <template
                                        v-if="item.type == 1 && item.rate !== 1 && item.countStu || item.type == 10 && item.rate !== 1 && item.countStu">正在批改中...</template>
                                    <template
                                        v-if="item.type == 0 && item.rate !== 1 && item.countStu">正在统计中...</template>
                                    <template
                                        v-if="item.endTime">结束于{{ item.endTime | formatDate}}，用时{{ item.withTime }}</template>
                                </div>
                            </div>
                            <div class="btn_check" @click="checkTask(item.id, item.taskId, item.type)" v-if="!((item.type == 1||item.type == 10)&&item.countStu==0)">查看</div>
                        </div>
                        <div class="progress" v-if="item.countStu"
                            v-show="item.type === 0 || item.type === 1|| item.type === 10 ">
                            <div class="percent" :style="{width:  item.rate*100 +'%'}"></div>
                        </div>
                    </div>
                </vue-scroll>
            </div>
            <div class="footer">
                <div class="btn_close" @click="closeWindow" :class="{isHistoryPadding:isHistory}">
                    <img src="@/assets/icon_close@2x.png" />
                </div>
            </div>

        </div>
    </div>
</template>
<script>
    import { formatDate } from '@/assets/date.js';
    import utils from '@/assets/utils.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    var Base64 = require('js-base64').Base64;
    var timer;



    export default {
        name: 'App',
        data() {
            return {
                isHistory: 0 ,
                records: null,
                taskIds: null
            }
        },
        filters: {
            formatDate(time) {
                let date = new Date(time);
                return formatDate(date, 'hh:mm');
            },
            decodePaperName(str) {
                return Base64.decode(str.split(",")[0].split(":")[1]);
            }
        },
        mounted() {
            //注册需要外部调用的方法
            window.setPercent = this.setPercent
            //获取url参数
            let classingId = utils.getUrlPrams("id")
            let isHistory = utils.getUrlPrams("isHistory")
            if(isHistory){
                this.isHistory = isHistory
            }
            this.getRecords(classingId)
        },
        methods: {
            backCalendar() {
                history.back()
            },
            closeWindow() {
                localStorage.removeItem("currDay")
                window.cefQuery({
                    request: "closeWindowRecords",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            getRecords(id) {
                var that = this
                axios.get(API.api_tl + 'api/selClassRecord', {
                    params: {
                        classingId: id
                    }
                }).then(function (res) {
                    console.log(res.data.data)
                    let records = res.data.data
                    let taskIds = []
                    if (records) {
                        for (let record of records) {
                            if (record.type === 0 || record.type === 1 || record.type === 10) {
                                if (record.rate !== 1) {
                                    taskIds.push(record.taskId)
                                }
                            }
                        }
                        that.records = records
                        that.taskIds = taskIds
                    }
                    if (that.taskIds.length > 0) {
                        that.getTaskData()
                        setInterval(() => {
                            that.getTaskData()
                        }, 1000);
                    }
                    // 通过$once来监听定时器，在beforeDestroy钩子可以被清除。
                    that.$once('hook:beforeDestroy', () => {
                        clearInterval(timer);
                    })
                }).catch(function (error) {
                    console.log(error);
                });
            },
            getTaskData() {
                var that = this
                if (this.taskIds.length > 0) {
                    axios.get(API.api_qb + 'statistics/getTaskDynamic', {
                        params: {
                            taskIds: that.taskIds.join(",")
                        }
                    }).then(function (res) {
                        console.log(res.data.data)
                        let tasks = res.data.data
                        for (let task of tasks) {
                            console.log(task)
                            that.setPercent(task.deviceNumbers, task.correctDeviceNumbers, task.taskId, "correcting")
                        }
                    }).catch(function (error) {
                        console.log(error);
                    });
                }

            },
            setPercent(count, curr, taskId, status = "uploading") {
                console.log(count, curr, taskId, status)
                let records = this.records
                let id = taskId
                for (let i = 0; i < records.length; i++) {
                    console.log(i)
                    if (records[i].taskId == id) {
                        this.$set(this.records[i], "countStu", count);
                        this.$set(this.records[i], "status", status);
                        this.$set(this.records[i], "rate", (curr * 100 / count) / 100);
                    }
                }
                console.log(this.records)
                //如果进度为100%,则不再请求该task
                if (status === "correcting" && count === curr) {
                    let index = this.taskIds.indexOf(taskId)
                    this.taskIds.splice(index, 1)
                    if (this.taskIds.length === 0) {
                        clearInterval(timer);
                    }
                } else {
                    // setTimeout(() => {
                    //     this.getTaskData()
                    // }, 1000);
                }
            },
            checkTask(id, taskId, type) {
                let value = "";
                if (type === 0 || type === 1 || type === 10) {
                    value = taskId
                } else {
                    value = id
                }
                console.log(value)
                window.cefQuery({
                    request: "checkTask:" + value + "&" + type,   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            }
        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }

    body {
        width: 100%;
    }

    #app {
        font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "微软雅黑", Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        text-align: center;
        color: #2c3e50;
    }
</style>
<style scoped>
    .window_con {
        width: 100%;
        height: 100vh;
        border-radius: 0.2rem;
        background: #fff;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
    }

    .isHistoryPadding{
        padding: 0.2rem
    }

    .records {
        width: 100%;
        height: 80vh;
        margin: 0 auto;
        box-sizing: border-box;
        padding: 0.2rem
    }

    .item {
        height: 1rem;
        border-bottom: 1px solid rgba(51, 81, 102, .15);
        margin: 0 0.3rem;
    }

    .info {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 1rem;
    }

    .info_left {
        flex: 1;
        text-align: left;
    }

    .info_left .type {
        font-size: 0.16rem;
        font-weight: 400;
        color: #2D4A66;
        line-height: 0.33rem;
        margin-bottom: 0.1rem;
    }

    .info_left .user {
        font-size: 0.14rem;
        font-weight: 400;
        color: #AEBDD6;
        line-height: 1.5;
    }

    .btn_check {
        font-size: 0.16rem;
        font-weight: 400;
        color: #2D4A66;
        line-height: 0.35rem;
        width: 0.65rem;
        height: 0.35rem;
        border-radius: 0.03rem;
        cursor: pointer;
        border: 1px solid #8BADCE;
    }

    .progress {
        height: 0.04rem;
        background: #9DBEDA;
        border-radius: 0.02rem;
    }

    .progress .percent {
        height: 0.04rem;
        background: #00D97F;
        border-radius: 0.02rem;
        opacity: 0.8025;
        transition: 0.3s all linear;
    }

    .footer {
        height: 10vh;
        display: flex;
        justify-content: space-around;
        align-items: center;
    }
    
    .btn_back{
        font-size: 0.16rem;
        width: 1rem;
        padding: 0.3rem;
        text-align: left;
        color: #2E4A66;
        vertical-align: middle;
        position: absolute;
        left: 0;
        top: 0;
    }

    .btn_back img{
        display: inline-block;
        width: 0.18rem;
        height: 0.18rem;
        vertical-align: middle;
        margin-right: 0.1rem;
    }
</style>